import empty from "@/components/empty";
export default {
	components: {
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			navList: [{state: 0,text: '全部'},
				{state: 1,text: '待预约'},
				{state: 2,text: '待付款'},
				{state: 3,text: '待服务'},
				{state: 4,text: '待评价'},
				{state:5,text: '售后'}
			],
		};
	},
	
	onLoad(options){
		this.tabCurrentIndex = +options.state||0;
	},
	 
	methods: {
		//顶部tab点击
		tabClick(index){
			this.tabCurrentIndex = index;
		},
		
	},
}
